
:root {
	--gutter: 20px;
	--interaction-color: #42b983;
}
@media (min-width: 600px) {
:root {
		--gutter: 24px;
}
}
@media (min-width: 800px) {
:root {
		--gutter: 32px;
}
}
@media (min-width: 1200px) {
:root {
		--gutter: 48px;
}
}
.container {
	max-width: 1600px;
	width: 100%;
	padding-right: var(--gutter, 16px);
	padding-left: var(--gutter, 16px);
	margin-right: auto;
	margin-left: auto;
	box-sizing: border-box;
}

